body{
    background-image: url("../assets/images/3.JPEG");
    background-position: center;
    background-repeat: no-repeat;
    background-size:2000px;
    background-position: -31px -300px;
    backdrop-filter: blur(9px);
}
.content{
    width: 500px;
    height: 400px;
    border: solid 3px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    background-color: rgba(9, 27, 59, 0.85);
    border-style:outset ;
    text-align: center;
    box-shadow: 25px 13px 23px 9px rgba(0,0,0,0.1);
    padding: 20px;

}

.subscribeInput{
    margin: 8px;
    border-radius: 10px;
    border: solid white;
    border-style:dotted;
    height: 30px;
    width: 300px;
}
.subscribeButton{
    margin-top: 30px;
    width: 50%;
    height: 50px;
    background-color:yellowgreen;
    color: #0938c8;
    border: solid 2px;
    box-shadow: 25px 13px 23px 9px rgba(0,0,0,0.1);
    border-radius:50px;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
    font-size: x-large;
    font-weight: 500;
    border-style: outset;
}

h3{
    margin-left: 100px;
    margin-top: 50px;
    width: 300px;
}
h1{
    color:rgb(91, 141, 242);
}

.subscribeButton:hover{
    background-color:rgb(111, 160, 12);
}
.subscribeButton:active{
    background-color:rgb(101, 146, 11);
}   
h4{
    position: absolute;
    font-size: 15px;
    margin: -5px 135px;
    color: rgb(255, 0, 0);
}

@media screen and (max-width: 640px) {
    html{
        width: 100%;
    }
    body{
        background-image: url("../assets/images/3.JPEG");
        background-position: center;
        background-repeat: no-repeat;
        background-size:1500px;
        background-position: -545px -250px;
        backdrop-filter: blur(5px);
    }
    .content{
        height: 500px;
        background-color: rgba(9, 27, 59, 0.75);
    }
}